<script>
	//import pages from './pages'
	
	
	import Page0 from './pages/01_Introduction.svx'
import Page1 from './pages/02_GettingStarted.svx'
import Page2 from './pages/03_Navigation.svx'
import Page3 from './pages/04_Layouts.svx'
import Page4 from './pages/05_Default.svx'
import Page5 from './pages/06_Centered.svx'
import Page6 from './pages/07_Paged.svx'
import Page7 from './pages/08_Classes.svx'
import Page8 from './pages/09_Automated.svx'

	const pages = [Page0,Page1,Page2,Page3,Page4,Page5,Page6,Page7,Page8]
	 

	let index = 0;

	const next = () => index !== pages.length - 1 && index++
	const prev = () => index !== 0 && index--

	const handlekey = async (ev) => {
        ev.key === 'ArrowRight' && next()
        ev.key === 'ArrowLeft' && prev()
	}

	console.log("Sun Aug 09 2020 17:17:53 GMT+0200 (Central European Summer Time)")

</script>

<style>
	button {
		background-color: white;
		border: 2px solid #c0c0c0;
		border-radius: .5rem;
		bottom: 1rem;
		color: black;
		padding: .5rem 1rem;
		position: fixed;
	}

	button:active,
	button:focus,
	button:hover {
		background-color: #c0c0c0;
	}
	.prev { left: 2rem; }
	.next { right: 2rem; }
</style>

<svelte:window on:keyup={handlekey}></svelte:window>

<svelte:component this={pages[index]} />

{#if index !== 0}
	<button class="prev" on:click="{prev}">&lt; Previous</button>
{/if}

{#if index !== pages.length - 1}
	<button class="next" on:click="{next}">Next &gt;</button>
{/if}